@media screen and (max-width: 720px){
    .main-first-container {
        width: 100%;
        height: 400px; /* Altezza della tua immagine */
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .wave-top {
        width: 100%;
        height: auto;
        position: relative;
        bottom: -85%;
    }

    .main-second-container {
        padding-top: 30%;
        height: 50vh;
    }

    .wave-bottom{
        width: 100%;
        height: auto;
        position: relative;
        bottom: -60%;
    }

    .main-third-container{
        height: 100px;
    }
}


@media screen and (max-width: 400px) {
    .main-second-container{
        color: green;
    }
}